body,
html {
    width: 100%;
    height: 100%;
}

body,
h2,
h3,
h4,
h5,
h6 {
    font-family: "Lato", "Helvetica Neue", Helvetica, Arial, sans-serif;
    font-weight: 100;
    letter-spacing: 3px;

}

.topnav {
    background-color: #333;
    overflow: hidden;
}

/* Style the links inside the navigation bar */
.topnav a {
    float: left;
    color: #f2f2f2;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
    font-size: 17px;
}

/* Change the color of links on hover */
.topnav a:hover {
    background-color: #ddd;
    color: black;
}

/* Add a color to the active/current link */
.topnav a.active {
    background-color: #6363d5;
    color: white;
}

/**********************************
NAVIGATION
***********************************/
#brand {

    height: 40px;
    width: 50px;
}

.brand-title {
    color: white;
    font-family: 'lato';
    text-align: left;
    margin: 7% auto;
    font-size: 1em;
    font-weight: 800;
    letter-spacing: 1;
    text-transform: uppercase;
}

.navigation {
    margin-left: 50px;
    background: none;
    border: none;
}

.scrollNavigation {
    margin-right: 30px;
    background: #19171D;
    border: none;
    text-align: center;
    width: 100%;
}


#crunch {
    color: white;
}

#menuText {
    font-weight: 150;
    font-size: 18px;
    color: white;
    padding: 10px 30px 10px 0px;
}

#menuTextLeft {
    font-weight: 150;
    font-size: 18px;
    color: white;
    padding: 10px 0px 0px 0px;
}


#navButton {
    font-weight: 500;
    font-size: 20px;
    border: none;
    padding: 1;
    margin: 18px 10px 10px 0px;
    border-radius: 0%;
    color: white;
    background: #EF494F;
}

#navButton:hover {
    font-weight: 100;
    font-size: 20px;
    background: #9D2226;
    border: none;
    padding: 1;
    margin: 18px 10px 10px 0px;
    border-radius: 0%;
    color: white;
}

a,
ul,
li {

    text-decoration: none;
}

#search {

    margin: 25px 30px 10px 0px;
    color: white;

}

#search:hover {

    -moz-transform: scale(1.1);
    -webkit-transform: scale(1.1);
    transform: scale(1.1);

}

