/**********************************
Body
***********************************/

.learn:hover {
    cursor: pointer;
}

.arrow:hover {

    text-align: center;
    cursor: pointer;

}

.intro-header {
    padding-top: 50px;
    padding-bottom: 50px;
    text-align: left;
    color: #f8f8f8;
    background: linear-gradient(rgba(184, 188, 191, 0.6),
            rgba(79, 128, 207, 0.7)), url("https://www.tatratrucks.com/cache/images/galleryPreviewBig/tatra_79pr39_podvozek.jpg");
    background-size: cover;

}

.intro-message {
    position: relative;
    padding-top: 10%;
    padding-bottom: 20%;
}

.intro-message>h1 {
    font-size: 4em;
    font-weight: 400;
    text-align: center;

}

.intro-message>h3 {
    margin-top: 50;
    font-size: 2.5em;
    font-weight: 150;
    letter-spacing: 4;
    text-align: center;

}

.intro-message>h4 {
    margin-top: 200;
    font-size: 1.25em;
    font-weight: 400;
    letter-spacing: 2;

}

.landImg {

    margin-left: 15%;
    margin-top: 5%;

}

.button {
    margin-top: 50px;
    margin-bottom: 20px;
    background: #AFDAA6;
    border: none;
    border-radius: 0%;
    border-bottom: 6px solid #99bc91;
    padding: 15px 15px 15px 15px;

}


.button2 {
    margin-top: 50px;
    margin-bottom: 20px;
    background: #F0777E;
    border: none;
    border-radius: 0%;
    border-bottom: 6px solid #dd6c72;
    padding: 15px 15px 15px 15px;

}

.button:hover {
    margin-top: 55px;
    height: 95%;
    background: #AFDAA6;
    border-bottom: 0px solid #AFDAA6;
    border-radius: 0%;
    padding: 21px 15px 15px 15px;

}

.button2:hover {
    margin-top: 50px;
    background: #F0777E;
    border-bottom: 6px solid #F0777E;
    border-radius: 0%;
    padding: 21px 15px 15px 15px;

}


.highlight {

    color: #AFDAA6;
